.but_full2 {
	width: 100%;
	height: 70px;
	background-color: #f3f3f3;
	overflow: hidden;
	text-align: center;
	color: #272727;
	border-radius: 4px;
	vertical-align: middle;
	line-height: 70px;
	font-size: 18px;
	font-weight: 600;
}
.but_full2:hover {
	color: #fff;
}
.but_full2 .butprogress {
	-webkit-transition: all 0.7s ease;
	-moz-transition: all 0.7s ease;
	-ms-transition: all 0.7s ease;
	-o-transition: all 0.7s ease;
	transition: all 0.7s ease;
	height: 70px;
	width: 10px;
	background: #04ace2;
	position: absolute;
	overflow: hidden;
	border-radius: 4px;
	border-top-right-radius: 0px;
	border-bottom-right-radius: 0px;
}
.but_full2 .done {
	float: left;
	text-align: center;
	line-height: 70px;
	font-size: 18px;
	font-weight: bold;
	color: #EEE;
	height: 70px;
	width: 100%;
	text-shadow: 0px -1px 0px rgba(0,0,0,0.2);
	cursor: pointer;
	vertical-align: middle;
	line-height: 70px;
	font-weight: 600;
	position: relative;
	z-index: 98;
}
.but_full2 strong {
	position: relative;
	z-index: 99;
	font-weight: bold;
	text-transform: uppercase;
}
.but_full2:hover .butprogress {
	-webkit-transition: all 0.7s ease;
	-moz-transition: all 0.7s ease;
	-ms-transition: all 0.7s ease;
	-o-transition: all 0.7s ease;
	transition: all 0.7s ease;
	width: 100%;
	border-radius: 4px;
}
.but_full2 .butprogress a {
	color: #fff;
}
.but_full2.two .butprogress {
	background: #000;
}
